import React, { Component } from 'react';
import './contato.css';

class contact extends Component {
  state = {};
  render() {
    return (
      <>
        <div className='contato'>
          <span style={{ fontSize: 35, color: '#fff' }}>Area para contato</span>
        </div>
        <div className='contatoDiv'></div>
      </>
    );
  }
}

export default contact;
